/* css variables */
:root {
	--main-color:#158FCA;
	--secondary-color:#ffffff;
	--main-text-color:#3e5869;
	--secondary-text-color:#b0c7d6;
	--send-message-form:#f5f5f5;
}

/* CSS Grid */
.app {
	display: grid;
	height: 100%;
	grid-template-columns: repeat(6, 1fr);
	grid-template-rows: repeat(5, 1fr) 3.75rem;
	grid-template-areas:
			"r m m m m m"
			"r m m m m m"
			"r m m m m m"
			"r m m m m m"
			"r m m m m m"
			"n s s s s s"
}
.new-room-form {
	grid-area: n;
}
.room-list {
	grid-area: r;
}
.message-list {
	grid-area: m;
}
.send-message-form {
	grid-area: s;
}

/* CSS Styling */
.app {
	color: var(--main-text-color);
}
.room-list {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	padding: .625rem;
	background-color: var(--main-color);
	overflow-y: auto;
	height: 100%;
}
.room-list ul {
	list-style-type: none;
	padding: 0;
	overflow-y: auto;
}
.room-list li {
	margin: .625rem 0;
}
.room-list h3 {
	margin: .3125rem 0;
	color: var(--secondary-color);
	font-size: 1.4rem;
}
.room-list .room a {
	color: var(--secondary-text-color);
	font-weight: 600;
	text-decoration: none;
}
.room-list .room.active a {
	color: var(--secondary-color);
}
.new-room-form {
	padding: 0 .3125rem;
	background: var(--secondary-color);
	color: var(--main-text-color);
}
.new-room-form form {
	height: 100%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	-ms-flex-align: center;
	    align-items: center;
}
.new-room-form input {
	width: 135px;
	background: var(--secondary-color);
}

.new-room-form button {
	background: var(--secondary-color);
	color: var(--main-text-color);
	border: 0;
}

.new-room-form input::-webkit-input-placeholder {
	color: var(--main-text-color);
	font-weight: 200;
}

.new-room-form input:-ms-input-placeholder {
	color: var(--main-text-color);
	font-weight: 200;
}

.new-room-form input::-ms-input-placeholder {
	color: var(--main-text-color);
	font-weight: 200;
}

.new-room-form input::placeholder {
	color: var(--main-text-color);
	font-weight: 200;
}

.new-room-form input:focus {
	outline-width: 0;
}

.new-room-form input {
	border: 0;
}

.new-room-form button {
	border: 0;
}

.message {
	margin: 15px 0;
}

.message .message-username {
	font-size: 11px;
	color: var(--main-text-color);
	opacity: 0.9;
	margin-bottom: 6px;
}
.message .message-text {
	background: var(--main-color);
	color: var(--secondary-color);
	display: inline;
	padding: 4px 8px;
	border-radius: 8px;
}

.message-list {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	padding-left: 6px;
	width: 100%;
	height: 100%;
	overflow-y: auto;
	background: var(--secondary-color);
}

.message-list .join-room {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	height: 100%;
	font-size: 34px;
	font-weight: 300;
}

.send-message-form {
	background: var(--send-message-form);
	display: -ms-flexbox;
	display: flex;
}

.send-message-form input {
	width: 100%;
	padding: 15px 10px;
	margin: 0;
	border-style: none;
	background: var(--send-message-form);
	font-weight: 200;

}

.send-message-form input:focus {
	outline-width: 0;
}

.send-message-form input::-webkit-input-placeholder {
	color: var(--main-text-color);
}

.send-message-form input:-ms-input-placeholder {
	color: var(--main-text-color);
}

.send-message-form input::-ms-input-placeholder {
	color: var(--main-text-color);
}

.send-message-form input::placeholder {
	color: var(--main-text-color);
}

.help-text {
	position: absolute;
	top: 10px;
}



